


























import Vue from 'vue';
import OverflowIndicator from '@/configurator/components/parameters/OverflowIndicator.vue';

function newResizeObserver(callback: () => void) {
  // Skip this feature for browsers which
  // do not support ResizeObserver.
  // https://caniuse.com/#search=resizeobserver
  // @ts-ignore
  if (typeof ResizeObserver === 'undefined') {
    return;
  }

  // @ts-ignore
  return new ResizeObserver((e) => e.map(callback));
}

export default Vue.extend({
  components: {
    OverflowIndicator,
  },
  data() {
    return {
      hint: {
        top: false,
        right: false,
        bottom: false,
        left: false,
      },
    };
  },
  mounted() {
    // Check if shadows are necessary after the element is resized.
    const scrollContainerObserver = newResizeObserver(this.toggleHints);
    if (scrollContainerObserver) {
      scrollContainerObserver.observe(this.$refs.scrollContainer);
      // Cleanup when the component is destroyed.
      this.$once('hook:destroyed', () => scrollContainerObserver.disconnect());
    }
  },
  updated() {
    this.toggleHints();
  },
  methods: {
    scrollRight() {
      const scrollContainer = this.$refs.scrollContainer as HTMLDivElement;
      scrollContainer.scrollBy({left: scrollContainer.clientWidth, behavior: 'smooth'});
    },
    scrollLeft() {
      const scrollContainer = this.$refs.scrollContainer as HTMLDivElement;
      scrollContainer.scrollBy({left: -scrollContainer.clientWidth, behavior: 'smooth'});
    },
    toggleHints() {
      const distanceInPixel = 30;
      const scrollContainer = this.$refs.scrollContainer as HTMLDivElement;
      const hasHorizontalScrollbar = scrollContainer.clientWidth < scrollContainer.scrollWidth;
      const hasVerticalScrollbar = scrollContainer.clientHeight < scrollContainer.scrollHeight;

      const scrolledFromLeft = scrollContainer.offsetWidth + scrollContainer.scrollLeft;
      const scrolledFromTop = scrollContainer.offsetHeight + scrollContainer.scrollTop;

      const scrolledToTop = scrollContainer.scrollTop <= distanceInPixel;
      const scrolledToRight = scrolledFromLeft + distanceInPixel >= scrollContainer.scrollWidth;
      const scrolledToBottom = scrolledFromTop + distanceInPixel >= scrollContainer.scrollHeight;
      const scrolledToLeft = scrollContainer.scrollLeft <= distanceInPixel;

      this.hint.top = hasVerticalScrollbar && !scrolledToTop;
      this.hint.right = hasHorizontalScrollbar && !scrolledToRight;
      this.hint.bottom = hasVerticalScrollbar && !scrolledToBottom;
      this.hint.left = hasHorizontalScrollbar && !scrolledToLeft;
    },
  },
});

