

















































import Vue, {PropOptions} from 'vue';
import OptionsParameterValue from '@/configurator/components/parameters/types/OptionsParameterValue.vue';
import {UI_STATE_ACTIONS, UI_STATE_MUTATIONS} from '@/configurator/store/ui-state';
import {ParameterGroup} from '@/configurator/store/core-data';
import ScrollHint from '@/configurator/components/utils/ScrollHint.vue';
import ParameterGroupArrowButton from '@/configurator/components/parameters/ParameterGroupArrowButton.vue';
import ParameterGroupButton from '@/configurator/components/parameters/ParameterGroupButton.vue';
// @ts-ignore
import SvgClose from '@/common/svgs/close.svg';
// @ts-ignore
import SvgVariants from '@/common/svgs/variants.svg';

export default Vue.extend({

  components: {
    OptionsParameterValue,
    ScrollHint,
    ParameterGroupArrowButton,
    ParameterGroupButton,
  },

  props: {
    isExpanded: Boolean,
    selectedGroup: Object as PropOptions<ParameterGroup>,
    groups: Array as PropOptions<ParameterGroup[]>,
  },

  computed: {
    icons(): object {
      return {
        SvgClose,
        SvgVariants,
      };
    },
    showBackButton(): boolean {
      return !!this.$store.state.uiState.currentSelection;
    },
    showBackButtonText(): boolean {
      if (!this.groups.length) {
        return true;
      }
      return false;
    },
    showVariants(): boolean {
      return !this.showBackButton && !!this.$store.state.uiState.currentVariants;
    },
  },

  methods: {
    async setGroup(key: string) {
      const selectedGroup = (this.groups as ParameterGroup[]).find((group) => group.key === key);
      if (selectedGroup) {
        this.$store.dispatch(UI_STATE_ACTIONS.SELECT_GROUP, selectedGroup);
        this.$sdkConnector.configuratorApi.then((configurator) => configurator.setActiveGroupInView(selectedGroup.key));
      }
    },
    cancelSelection() {
      this.$sdkConnector.configuratorApi.then((configurator) => configurator.cancelSelection());
    },
    openVariants() {
      this.$store.dispatch(UI_STATE_ACTIONS.RESET_COLLECTION_VIEW);
      this.$store.commit(UI_STATE_MUTATIONS.SET_SHOW_VARIANTS, true);
    },
  },

});
